
















export default {
	data() {
		return {};
	},
	computed: {
		list() {
			return this.$store.state.list;
		}
	},
	methods: {
		open_loading() {
			this.$loading();
		},
		get_detail(id) {
			uni.navigateTo({
				url: '/pages/detail/detail?id=' + id
			});
		},
		test_get() {
			this.$ajax
				.get({
					url: '/admin/get_product_list',
					data: {
						a: 1
					}
				})
				.then(res => {
					this.$alert('状态码：' + res.code);
					console.log(res);
				});
		},
		test_post() {
			// 基本同GET
			this.$ajax
				.post({
					url: '/admin/get_product_list'
				})
				.then(res => {
					this.$alert(res.code);
					console.log(res);
				});
		}
	}
};
